@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?b15107');
  src:  url('fonts/icomoon.eot?b15107#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?b15107') format('truetype'),
    url('fonts/icomoon.woff?b15107') format('woff'),
    url('fonts/icomoon.svg?b15107#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-instagram:before {
  content: "\e900";
}
.icon-music:before {
  content: "\e911";
}
.icon-play:before {
  content: "\e912";
}
.icon-envelop:before {
  content: "\e945";
}
.icon-twitter:before {
  content: "\ea96";
}
.icon-onedrive:before {
  content: "\eaaf";
}
